import axios, { AxiosResponse } from "axios";
import { VariantFilterModel } from "./models/VariantFilterModels";
import { VariantResponse } from "./models/VariantResponse";

export default class OrderApiService {
  static async getTestVariants(
    filter: VariantFilterModel
  ): Promise<VariantResponse[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        return resolve(
          sampleVariants.filter(
            (v) =>
              !filter?.keyword ||
              v.name.includes(filter.keyword) ||
              v.sku?.toLowerCase() === filter.keyword.toLowerCase()
          )
        );
      }, Math.random() * 500);
    });
  }
}

